body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}
ul#nav {
	list-style-type: none;
	top: -5px;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: #333;
}

#nav li {
	float: right;
	display: block;
	color: white;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;
}
.active {
	background-color: rgb(66, 183, 255);
}
#nav li:hover :not(.active) {
	background-color: #111;
}


